/* Resetting some default browser styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


html {
  scroll-behavior: smooth;
}

body {
  font-family: 'MessinaSansWeb', sans-serif;
}

/* Container for consistent width throughout the sections */
.container, .inner-container {
  max-width: 1200px;   /* Limit content width to ensure alignment */
  margin: 0 auto;
  padding: 0 20px;     /* Padding inside the container for spacing */
}

/* Default Light Mode */
/* Light Mode Variables */
body.light {
  --bg-color: #ffffff;
  --text-color: #333333;
  --accent-color: #e63946;
  --card-bg-color: #f8f9fa;
  --tag-bg-color:#333333;
  --tag-txt-color:#ffffff;
  --button-bg-color: #e63946;
  --button-text-color: #ffffff;
  --link-color: #1d3557;
  --icon-color:#333;
}

/* Dark Mode Variables */
body.dark {
  --bg-color: #121212;
  --text-color: #f4f4f9;
  --accent-color: #f4f4f9;
  --card-bg-color: #1e1e1e;
  --tag-bg-color:#EDF7FA;
  --tag-txt-color:#333333;
  --button-bg-color: #e63946;
  --button-text-color: #ffffff;
  --link-color: #a8dadc;
  --icon-color:#ffffff;
}

body {
  background-color: var(--bg-color);
  color: var(--text-color);
}

a {
  color: var(--link-color);
}

.header a {
  color: var(--text-color);
}

button {
  background-color: var(--button-bg-color);
  color: var(--button-text-color);
}

/* Accent color for emphasis */
.accent {
  color: var(--accent-color);
}


.theme-toggle-btn {
  background-color: #333333; /* Light gray background */
  border: none;
  border-radius: 13px; /* Rounded edges */
  padding: 10px;
  margin-left: 15px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease; /* Smooth background and hover effect */
  height: 100%;
}

/* Add vertical alignment correction to the PNG icon */
.theme-icon {
  width: 30px;
  height: 30px;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  top: 2px; /* Adjust this value to align perfectly */
}

.theme-toggle-btn:hover {
  transform: scale(1.1);
}

/* Navbar styles - aligning to the right */
.header {
  display: flex;
  justify-content: flex-end;   /* Align navbar to the right */
  padding: 20px 0;
}

.navbar a {
  text-decoration: none;
  color: var(--text-color);
  font-weight: bold;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center; /* Aligns items vertically in the center */
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.navbar li {
  margin: 0 15px; /* Add spacing between nav items */
}

.header a {
  text-decoration: none;
  color: inherit;
  font-size: 1.1rem; /* Adjust as necessary */
  line-height: 24px; /* Match the icon height */
  display: inline-block; /* Ensures proper height alignment */
}

/* Intro section styles */
.intro-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 50px 20px;
}

.intro-content {
  max-width: 600px;
  flex-grow: 1;
  margin-right: 20px;
}

.intro-content h2 {
  font-size: 60px;
  margin-bottom: 10px;
}

.intro-content h3 {
  font-size: 30px;
  margin-bottom: 20px;
}

/* Typing container to prevent movement of other elements */
.typing-container {
  width: 100%; /* Takes full width of the content */
  height: 100px; /* Fixed height to prevent shifting */
  overflow: hidden; /* Hide overflow to contain text properly */
  font-family: roboto;
}

/* Typing Effect */
.typing-text {
  font-size: 18px;
  color: #777;
  white-space: pre-wrap; /* Respect line breaks */
  overflow: hidden;      /* Hide overflow during typing */
  border-right: none;    /* Remove the blinking caret */
}

/* Highlight class to color the specific text in red */
.highlight {
  color: #f75959;
}

/* Styling for the download button */
.download-button {
  padding: 10px 20px;
  background-color: #f75959;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 16px;
  text-decoration: none; /* Remove underline from anchor tag */
  display: inline-block; /* Keep the button look */
  border-radius: 10px;
}

/* Profile image styling */
.intro-image {
  width: 400px;
  height: 400px;
  border-radius: 50%;
  overflow: hidden;
  flex-shrink: 0;
}

.intro-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* For smaller screens */
@media (max-width: 768px) {
  .intro-section {
    flex-direction: column;
    align-items: center;
  }

  .intro-content {
    margin-right: 0;
    margin-bottom: 20px;
    text-align: center;
  }

  .intro-image {
    width: 200px;
    height: 200px;
  }
}

/* work section */
.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  padding: 20px;
}

.projects-grid-container {
  padding: 20px;
  text-align: center;
}

/* Styling for the Projects Heading */
.projects-heading {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 10px; /* Add space between heading and grid */
  color: var(--text-color);
  text-align: left;
  margin-left: 15px;
}

.project-card {
  position: relative; /* Allows absolute positioning inside */
  background-color: var(--card-bg-color);
  padding: 15px;
  height: 300px; 
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease;
}

.project-card p {
  text-align: left;
  color: var(--text-color);
}

.project-card:hover {
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
}

/* Flexbox for title and icon */
.card-header {
  display: flex;
  z-index: 0; 
  justify-content: space-between;
  align-items: center;  /* Aligns the text and icon vertically in the center */
  margin-bottom: 10px;  /* Adds spacing between the header and the description */
}

.project-title {
  font-size: 20px;
  z-index: 0;
  font-weight: bold;
  margin-right: 10px;  /* Ensures some space between the title and icon */
  flex-grow: 1; /* Allows the title to grow and take up remaining space */
  text-align: left;
}

.github-icon, .tableau-icon {
  font-size: 1.8rem;
  z-index: 0;
  color: var(--icon-color);
  transition: color 0.3s ease;
}

.github-icon:hover, .tableau-icon:hover {
  color: #000;
}

.project-card:hover {
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
}

.project-card h3 {
  font-size: 20px;
  margin-bottom: 10px;
}

.project-card p {
  font-size: 1rem;
  margin-bottom: 15px;
}


/* Change GitHub Icon Color on Hover */
.github-icon:hover {
  color: #f75959;  /* GitHub Blue */
}

/* Change Tableau Icon Color on Hover */
.tableau-icon:hover {
  color: #f75959;  /* Tableau Orange */
}

.tags {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 15px;
}

.tag {
  background-color: var(--tag-bg-color);
  color: var(--tag-txt-color);
  padding: 5px 10px;
  border-radius: 20px;
  font-size: 0.875rem;
}

/* Style specifically for Tableau projects */
/* .tableau-project {
  padding: 0;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
} */

/* Ensure the Tableau project's image fills the entire card */
.project-image-container {
  width: 100%;
  height: 250px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0; /* Ensure the image is at the bottom layer */
  overflow: hidden;
}

.project-image {
  width: 100%;
  height: 100%;
  margin-top: 20%;
  background-size: cover;
  background-position: center;
  position: relative;
}

/* White haze overlay behind the title and icon */
.white-haze {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.3); /* Semi-transparent white haze */
  z-index: 1; /* White haze sits above the image but below the title and icon */
}


/* Styling for photography page */
.photography-page {
  padding: 0; /* Remove any extra padding from the photography page */
}

.photography-page h2 {
  text-align: left; /* Align title to the left */
  margin-left: 20px; /* Add margin to match the style of other sections */
  margin-bottom: 20px;
}

/* Blue background container (end-to-end) */
.gallery-container {
  background-color: #e8f4ff; /* Blue background */
  width: 100vw; /* Make the blue box span the entire width of the viewport */
  margin-left: calc(-50vw + 50%); /* Ensure the box goes end-to-end by resetting the body's margins */
  padding: 40px 0; /* Padding to create space inside the box */
}

/* Center the gallery grid inside the blue box */
.gallery {
  display: flex;
  justify-content: center; /* Center-align the grid */
  flex-wrap: wrap;
  gap: 20px; /* Spacing between the images */
  max-width: 1200px; /* Optional: Limit the maximum width of the gallery */
  margin: 0 auto; /* Center the gallery horizontally */
  padding: 0 20px; /* Optional: Padding for responsiveness on smaller screens */
}

.gallery img {
  width: 300px;
  height: 300px;
  object-fit: cover;
  border-radius: 10px;
  background-color: white; /* White background for images */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Optional: Shadow to give depth */
}


/* view-all messaging*/
.view-all-link {
  position: relative;
  color: #ff6b6b;
  font-weight: bold;
  text-decoration: none;
}

.view-all-link:hover::after {
  content: "You are going to exit the website and go to GitHub";
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #333;
  color: #fff;
  padding: 5px 10px;
  font-size: 12px;
  border-radius: 5px;
  white-space: nowrap;
  z-index: 10;
  opacity: 1;
  transition: opacity 0.3s ease;
}

.view-all-link::after {
  content: "";
  opacity: 0;
  transition: opacity 0.3s ease;
}

/*contact details*/
.contact-section {
  padding: 50px 0;
  text-align: left; /* Align text and content to the left */
  background-color: var(--section-bg-color);
}

.contact-container {
  max-width: 1200px;
  height:500px;
  margin: 0 auto;
  padding-left: 20px; /* Add padding to the left for spacing */
}

.contact-section h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

/* Horizontal line (section-line) styling */

.section-line {
  width: 600px;   /* Width of the line */
  height: 2px;    /* Thickness of the line */
  background-color: #E0E0E0;  /* Line color */
  margin: 0 auto 150px auto;  /* Center the line horizontally with 'margin: auto' */
}


.contact-section p {
  font-size: 1.2rem;
  margin-bottom: 30px;
}

.get-in-touch-btn {
  background-color: #f75959;
  color: white;
  padding: 15px 30px;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 10px; /* Add some margin to separate it from the text */
}

.get-in-touch-btn:hover {
  background-color: #e64553;
}

.get-in-touch-btn:focus {
  outline: none;
}


/* Footer styles */
.footer {
  padding: 40px 0;
  text-align: center;
}


.footer-links {
  display: flex;
  justify-content: center;        /* Center the social media icons */
  gap: 30px;                      /* Increase space between the icons */
  margin-bottom: 20px;            /* Space between icons and copyright text */
}

.footer a {
  text-decoration: none;
  color: #333;                    /* Dark gray color for icons */
  font-size: 30px;                /* Adjust icon size to be larger */
  transition: color 0.3s;         /* Smooth transition effect on hover */
}

.footer a:hover {
  color: #f75959;                 /* Change icon color to blue on hover */
}

.footer p {
  color: #777;
  font-size: 14px;
  margin-top: 20px;
}
